* { box-sizing: border-box; }

body {
  text-align: center;
}

.scene {
  margin: 40px 0;
  width: 210px;
  height: 140px;
  margin: 80px auto;
  perspective: 800px;
  opacity: 0;
  transition: all ease 2s;
}

.scene_grid {
  display: block;
}

.carousel {
  width: 100%;
  height: 100%;
  transform: translateZ(-288px);
  transform-style: preserve-3d;
  transition: transform 10s;
}

.carousel__cell {
  position: absolute;
  width: 190px;
  height: 120px;
  left: 10px;
  top: 10px;
  line-height: 116px;
  font-size: 80px;
  text-align: center;
  transition: transform 1s, opacity 1s;
}


.carousel__cell:nth-child(1) { transform: rotateY(  0deg) translateZ(288px); }
.carousel__cell:nth-child(2) { transform: rotateY( 24deg) translateZ(288px); }
.carousel__cell:nth-child(3) { transform: rotateY( 48deg) translateZ(288px); }
.carousel__cell:nth-child(4) { transform: rotateY( 72deg) translateZ(288px); }
.carousel__cell:nth-child(5) { transform: rotateY( 96deg) translateZ(288px); }
.carousel__cell:nth-child(6) { transform: rotateY( 120deg) translateZ(288px); }
.carousel__cell:nth-child(7) { transform: rotateY( 144deg) translateZ(288px); }
.carousel__cell:nth-child(8) { transform: rotateY( 168deg) translateZ(288px); }
.carousel__cell:nth-child(9) { transform: rotateY( 192deg) translateZ(288px); }
.carousel__cell:nth-child(10) { transform: rotateY( 216deg) translateZ(288px); }
.carousel__cell:nth-child(11) { transform: rotateY( 240deg) translateZ(288px); }
.carousel__cell:nth-child(12) { transform: rotateY( 264deg) translateZ(288px); }
.carousel__cell:nth-child(13) { transform: rotateY( 288deg) translateZ(288px); }
.carousel__cell:nth-child(14) { transform: rotateY( 312deg) translateZ(288px); }
.carousel__cell:nth-child(15) { transform: rotateY( 336deg) translateZ(288px); }
.carousel-options {
  text-align: center;
  position: relative;
  z-index: 2;
  background: hsla(0, 0%, 100%, 0.8);
}

.hiddenGrid {
  visibility: hidden;
}

