.contents2_group{
    display: flex;
    justify-content: center;
}
.contents2_box{
    width: 25vw;
    background: radial-gradient(133.8% 323.44% at 0% 0%, rgb(138, 143, 156) 0%, rgb(125, 123, 137) 100%);
    box-shadow: #ff3c42 0px 0px 5px, rgb(75 89 108 / 12%) 0px 8px 100px;
    border-radius: 30px;
    text-align: center;
}

.contents2 {
    height: 1200px;
}

.top {
    height: 40vw;
    display: grid;
    place-items: center;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-rows: 1fr;
    justify-content: center;
    grid-template-areas: 
    "title title title"
    "content content content";
}

.top h1 {
    grid-area: title;
}

@media (min-width: 1281px) {

}

/*

##Device = Laptops, Desktops

##Screen = B/w 1025px to 1280px

##Device = 랩탑, 데스크탑

##Screen = 1025px에서 1280px 사이

*/

@media (min-width: 1025px) and (max-width: 1280px) {


}

/*

##Device = Tablets, Ipads (landscape),

##Screen = B/w 768px to 1024px

##Device = 태블릿, 아이패드(세로),

##Screen = 768px에서 1024px 사이

*/

@media (min-width: 768px) and (max-width: 1024px) {

}


/*

##Device = Tablets, Ipads (portrait)

##Screen = B/w 768px to 1024px

##Device = 태블릿, 아이패드(가로)

##Screen = 768px에서 1024px 사이

*/


@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

}

/*

##Device = Low Resolution Tablets, Mobiles (Landscape)

##Screen = B/w 481px to 767px

##Device = 저해상도 태블릿, 모바일(가로)

##Screen = 481px에서 767px 사이

*/

@media (min-width: 481px) and (max-width: 767px) {
    .top {
        grid-template-columns: repeat(1, 30vw);
        grid-template-rows: repeat(3, 5vw);
        row-gap: 250px;
    }
    .contents2 {
        height: 2000px;
    }
    .top {
        height: 120vw;
    }
}

/*

##Device = Most of the Smartphones Mobiles (Portrait)

##Screen = B/w 320px to 479px

##Device = 대부분의 스마트폰 모바일 기기(세로)

##Screen = 320px에서 479px 사이

*/

@media (max-width: 480px) {
    .top {
        grid-template-columns: repeat(1, 30vw);
        grid-template-rows: repeat(3, 5vw);
        row-gap: 250px;
    }
    .contents2 {
        height: 2000px;
    }
    .top {
        height: 180vw;
    }
}

@media (max-width: 290px) {
    .top {
        grid-template-columns: repeat(1, 50vw);
        grid-template-rows: repeat(3, 5vw);
        row-gap: 200px;
    }
    .contents2 {
        height: 2000px;
    }
    .top {
        height: 300vw;
    }
}
