:root {
    --text-color: black;
    --background-color: white;
    --accent-color: red;
}

.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--background-color);
    padding: 8px 12px;
    z-index: 2;
}

.navbar_logo {
    font-family: KonaTitle;
    font-size: 1.875em;
    text-decoration: none;
    color: var(--accent-color);
}

.navbar_logoIcon {
    font-size: 30px;
    margin-right: 10px;
    color: #FF6D72;
}

.navBtn a {
    color: var(--text-color);
    text-decoration: none;
}

.navbar_menu {
    display: flex;
    list-style: none;
    padding-left: 0;
}

.navbar_menu li {
    padding: 8px 12px;
    color: var(--text-color);
}

.navbar_menu li:hover {
    background-color: var(--background-color);
    border-radius: 4px;
}

.navbar_links {
    list-style: none;
    color: var(--accent-color);
    display: flex;
}

.navbar_links li {
    padding: 8px 12px;
}

.navbar_toogleBtn {
    display: none;
    position: absolute;
    top: 14px;
    right: 14px;
    font-size: 24px;
    width: 30px;
    color: var(--accent-color);
    /* transform: translate(-50%, -50%);
    cursor: pointer; */
}

.menu-toggle div {
    background: var(--accent-color);
    height: 5px;
    margin-bottom: 5px;
    display: block;
    transform: rotate(0deg);
}

button {
    width: 30px;
    height: 20px;
    background: none;
    border: none;
    padding: 0;
    display: block;
    margin: 0 auto;
    cursor: pointer;
    outline: none;
}

button span {
    width: 100%;
    height: 3px;
    background: var(--accent-color);
    display: block;
    margin: 0;
    border-radius: 100px;
    position: relative;
    left: -10px;
    transition: 0.2s cubic-bezier(1, 0.3, 0, 0.3);
}

button span::before {
    content: "";
    width: 100%;
    height: 3px;
    background: var(--accent-color);
    display: block;
    position: absolute;
    top: -200%;
    border-radius: 100px;
    left: 10px;
    transition: 0.2s cubic-bezier(1, 0.3, 0, 0.3);
}

button span::after {
    content: "";
    width: 100%;
    height: 3px;
    background: var(--accent-color);
    display: block;
    position: absolute;
    bottom: -200%;
    border-radius: 100px;
    right: -5px;
    transition: 0.2s cubic-bezier(1, 0.3, 0, 0.3);
}

button:hover span {
    left: 5px;
    transition: 0.4s cubic-bezier(1, 0.3, 0, 0.3);
}

button:hover span::before {
    left: 0;
    transition: 0.4s cubic-bezier(1, 0.3, 0, 0.3);
}

button:hover span::after {
    right: 0;
    transition: 0.4s cubic-bezier(1, 0.3, 0, 0.3);
}

.button-toggled span {
    left: 5px;
    background: transparent;
    top: 5px;
    transition: 0.3s cubic-bezier(1, 0.3, 0, 0.3) !important;
}

.button-toggled span::before {
    left: 0;
    transform-origin: center-left;
    transform: rotate(-45deg);
    bottom: 5px;
    transition: 0.3s cubic-bezier(1, 0.3, 0, 0.3) !important;
}

.button-toggled span::after {
    right: 0;
    transform-origin: center-right;
    transform: rotate(45deg);
    top: -5px;
    transition: 0.3s cubic-bezier(1, 0.3, 0, 0.3) !important;
}

.button-toggled:hover span {
    left: 5px;
}

.button-toggled:hover span::before {
    left: 0;
}

.button-toggled:hover span::after {
    left: 0;
}

