##Device = 대부분의 스마트폰 모바일 기기(세로)

##Screen = 320px에서 479px 사이

*/

@media (max-width: 480px) {

    .navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 8px 24px;
    }

    .navbar_menu {
        display: none;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .navbar_menu li {
        width: 100%;
        text-align: center;
    }

    .navbar_links {
        display: none;
        justify-content: center;
        width: 100%;
        padding: 0;
    }

    .navbar_toogleBtn {
        display: block;
    }

    .navbar_menu.active,
    .navbar_links.active {
        display: flex;
    }

}
